'use scrict';
import { SEARCH } from '@/config/image.js';
import { waterTap } from "@/utils/util.js";

export default {
	data() {
		return {
			search: {
				icon: SEARCH,
				value: ""
			}
		}
	},
	computed: {
		handSearchValueThrottl() {
			return waterTap(this.handSearchValue, 1000);
		}
	},
	methods: {
		handSearchValue() {
			this.$emit("change", this.search.value);
		}
	}
}
